import React, { useEffect, useState } from 'react'
import './Home.css'
import axios from "axios"
import { ReactSearchAutocomplete } from 'react-search-autocomplete'
import { useHistory } from 'react-router'


const sugerenciaURL = "https://validacion.hgtsa.com.ar/lappa/localidades/sugerenciaget?localidad="
const options = {
    withCredentials: true,
    headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
    }
}



const Home = ({ theme }) => {
    /////////////////////////////////////////////////////////////////// DECLARACION VARIABLES
    const history = useHistory()
    const [sugerenciasOrigen, setSugerenciasOrigen] = useState([])
    const [sugerenciasDestino, setSugerenciasDestino] = useState([])
    const [inputData, setInputData] = useState({
        idOrigen: null,
        idDestino: null,
        origen: "",
        destino: ""
    })
    /////////////////////////////////////////////////////////////////// DECLARACION FUNCIONES
    const handleOnSearchOrigen = (string, results) => {
        if (results[0]) {
            setInputData({ ...inputData, origen: string, idOrigen: results[0].id })
        } else {
            setInputData({ ...inputData, origen: string, idOrigen: null })
            fetchGetSugerencias()
        }
    }
    const handleOnSearchDestino = (string, results) => {
        if (results[0]) {
            setInputData({ ...inputData, destino: string, idDestino: results[0].id })
        } else {
            setInputData({ ...inputData, destino: string, idDestino: null })
            fetchGetSugerencias()
        }
    }
    const handleOnHoverOrigen = (result) => {
        // the item hovered
    }
    const handleOnHoverDestino = (result) => {
        // the item hovered
    }
    const handleOnSelectOrigen = (item) => {
        // the item selected
        setInputData({ ...inputData, origen: item.name, idOrigen: item.id })
    }
    const handleOnSelectDestino = (item) => {
        // the item selected
        setInputData({ ...inputData, destino: item.name, idDestino: item.id })
    }
    const handleOnFocusOrigen = () => {
    }
    const handleOnFocusDestino = () => {
    }
    const handleSearch = () => {
        if (inputData.idOrigen && inputData.idDestino) {
            history.push("/buscar?origen=" + inputData.idOrigen + "&destino=" + inputData.idDestino + "&pagina=1")
        } else {
            alert("No hay coincidencias con esa busqueda.")
        }
    }
    async function fetchGetSugerencias() {
        await axios.get(sugerenciaURL + inputData.origen, { options }).then(res => {
            let tmp = []
            res.data.map((item, index) => {
                tmp.push({ id: item.id, name: item.name })
                return item
            })
            setSugerenciasOrigen(tmp)
        });
        await axios.get(sugerenciaURL + inputData.destino, { options }).then(res => {
            let tmp = []
            res.data.map((item, index) => {
                tmp.push({ id: item.id, name: item.name })
                return item
            })
            setSugerenciasDestino(tmp)
        });
    }
    useEffect(() => {
        fetchGetSugerencias()
    }, [inputData.origen, inputData.destino])
    return (
        <div className='homeContainer'>
            <div className='rsac1' style={{ width: "40vmax" }}>
                <ReactSearchAutocomplete placeholder="Localidad de origen"
                    items={sugerenciasOrigen}
                    onSearch={handleOnSearchOrigen}
                    onHover={handleOnHoverOrigen}
                    onSelect={handleOnSelectOrigen}
                    onFocus={handleOnFocusOrigen}
                    autoFocus
                />
            </div>
            <div className='rsac2' style={{ width: "40vmax" }}>
                <ReactSearchAutocomplete placeholder="Localidad de destino"
                    items={sugerenciasDestino}
                    onSearch={handleOnSearchDestino}
                    onHover={handleOnHoverDestino}
                    onSelect={handleOnSelectDestino}
                    onFocus={handleOnFocusDestino}
                    autoFocus
                />
            </div>
            <div className={"buscar"} onClick={handleSearch}>
                <div>Buscar</div>
            </div>
        </div>)
}

export default Home;
