import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router'
import './Register.css'
import axios from 'axios'

const options = {
    withCredentials: true,
    headers: {
        "Access-Control-Allow-Origin": "*",
        "Content-Type": "application/json",
    }
}
const Register = ({ theme, setAnimation }) => {
    const history = useHistory()
    const [datos, setDatos] = useState({
        nombre: "",
        cuit: "",
        celular: "",
        email: "",
        localidad: ""
    })
    useEffect(() => {
    }, [])
    const handleInputChange = (e) => {
        setDatos({
            ...datos,
            [e.target.name]: e.target.value
        })
    }
    const handleSubmit = (event) => {
        if (datos.empresa === "" || datos.cuit === "" || datos.telefono === "" || datos.ubicacion === "") {
            alert("Hay campos sin rellenar, revise el formulario.")
        } else {
            axios.post("https://validacion.hgtsa.com.ar/lappa/usuarios/registro", datos, options).then(res => {
                console.log(res.data);
                history.push("/registered")
            })
        }
    }
    return (<div className="registerContainer">
        <div className="absolute">
            <img alt="" src="https://hgtarg.com/lappa/Marca_de_agua_lappa.png" className="marcadeagua" />
        </div>
        <p className='tituloRegister bold'>REGISTRARME GRATIS</p>
        <div className="formulario">
            <input type="text" placeholder="Razón social" className={"form-input"} onChange={handleInputChange} name="nombre"></input>
            <input type="text" placeholder="CUIT" className={"form-input"} onChange={handleInputChange} name="cuit"></input>
            <input type="tel" placeholder="Teléfono" className={"form-input"} onChange={handleInputChange} name="celular"></input>
            <input type="text" placeholder="Correo electrónico" className={"form-input"} onChange={handleInputChange} name="email"></input>
            <input type="text" placeholder="Localidad" className={"form-input"} onChange={handleInputChange} name="localidad"></input>
            <button className="botonRegistrar bold" onClick={handleSubmit}>REGISTRARME</button>
        </div>
    </div>)
}
export default Register;