import React, { useEffect, useState } from 'react'
import './NavBar.css'
import { useHistory } from 'react-router'
import light from '../img/Light.png'
import dark from '../img/Dark.png'

const NavBar = ({ theme, setBackgroundImage, back }) => {
    const history = useHistory()
    const [navBarBackground, setNavBarBackground] = useState(prevstate => {
        if (history.location.pathname === "/"){
            return {class: "transp"}
        } else {
            return {class: ""}
        }
     })
    const Light = light;
    const Dark = dark;
    console.log(history.location.pathname);
useEffect(() => {
    return history.listen(location => {
        location.pathname === "/" ? (() => {
            setNavBarBackground({ class: "transp" });
        })() : (() => {
            setNavBarBackground({ class: "" });
    })()
})
}, [history])
return (<div className={'navBar' + " " + navBarBackground.class}>
    <div className='LappaLogo' onClick={() => {
        history.push('/')
    }} />
    <div className="row navButtons">
    {
        history.location.pathname === "/" ? <a onClick={() => {
            setNavBarBackground({ class: ""})
            history.push("/transportista")
            history.go(0)
        }}><p>{"SOY T"}</p><p>{" RANSPORTISTA"}</p></a> : <a onClick={() => {
            setNavBarBackground({class: "transp"});
            history.push("/");
            history.go(0);
        }}><p>PRINCIPAL</p></a>
    }
        <p>|</p>
        <a ><p>CONTACTO</p></a>
    </div>
</div>)
}

export default NavBar;
