import React, { useEffect, useState } from 'react'
import {
    // BrowserRouter as Router,
    HashRouter as Router,
    Switch,
    Route,
} from "react-router-dom";
import Home from './components/Home';
import styled, { ThemeProvider } from 'styled-components'
import { darkTheme, GlobalStyles, lightTheme } from './themes'
import './App.css'
import Register from './components/Register';
import Registered from './components/Registered';
import NavBar from './components/NavBar';
import Transportista from './components/Transportista';
import Buscar from './components/Buscar';
import Perfil from './components/Perfil';

const lightImg = "https://hgtarg.com/lappa/Principal%20clara.jpg"
const darkImg = "https://hgtarg.com/lappa/Principal%20oscuro.jpg"
const App = () => {
    const [theme, setTheme] = useState('Light')
    const back = window.matchMedia('(prefers-color-scheme: dark)').matches ? darkImg : lightImg;
    const [backgroundImage, setBackgroundImage] = useState("url(" + back + ")")
    useEffect(() => {
        // window.matchMedia('(prefers-color-scheme: dark)').matches ? setTheme('Dark') : setTheme('Light')
        setTheme("Dark")
    }, [])
    const StyledApp = styled.div`
    color: ${(props) => props.theme.fontColor};
    display: flex;
    flex-direction: column;
    width: 100vw
    `;
    function getRandomInt(min, max) {
        return Math.floor(Math.random() * (max - min)) + min;
    }
    return (
        <ThemeProvider theme={theme === 'Light' ? lightTheme : darkTheme}>
            <GlobalStyles />
            <div className="bgGral" style={{
                display: 'flex',
                position: "absolute",
                zIndex: -1,
                opacity: 0.3,
                backgroundImage: backgroundImage,
                backgroundSize: "cover",
                width: "100vw",
                height: "100vh",
                transition: "opacity 1s",
                opacity: 1
            }} />
            <div style={{ zIndex: 1, display: "flex" }}>
                <StyledApp>
                    <Router basename="/lappa">
                        <NavBar theme={theme}></NavBar>
                        <Switch>
                            <Route path='/registered' component={() => <Registered theme={theme} />} />
                            <Route path='/register' component={() => <Register theme={theme} />} />
                            <Route path='/transportista' component={() => <Transportista theme={theme} />} />
                            <Route path='/buscar' component={() => <Buscar theme={theme} />} />
                            <Route path='/perfil' component={() => <Perfil theme={theme} />} />
                            <Route path='/' component={() => <Home theme={theme} />} />
                        </Switch>
                    </Router>
                </StyledApp>
            </div>
        </ThemeProvider>)
}
export default App;